.nav__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 1rem;

    .nav__div-button {
        margin: .5rem;
        width: 5em;
        border: #ffffff solid 3px;

        .nav__button {
            width: 100%;
            height: 100%;
            text-decoration: none;

            .nav__button-text {
                font-size: 16px;
                font-weight: 700;
                margin: .5rem;
                color: #ffffff;
            }
        }
    }
}